nav {
  height: 5rem;
  width: 100vw;
  background: var(--color-primary);
  display: grid;
  place-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
}

.nav__toggle-btn {
  display: none;
}
.nav__container {
  height: 100%;
  display: flex;
  position: relative;
  justify-content: space-between;
  align-items: center;
}
.logo {
  width: 7rem;
  display: block;
}
.nav__links {
  display: flex;
  gap: 3.5rem;
}

.nav__links a {
  transition: var(--transition);
}
.nav__links a:hover {
  color: var(--color-secondary);
}

.active-nav {
  position: relative;
}
.active-nav::after {
  content: '';
  display: block;
  width: 1.2rem;
  height: 1.2rem;
  background: var(--color-primary);
  position: absolute;
  left: calc(50% - 0.6rem);
  transform: rotate(45deg);
  margin-top: 0.9rem;
}

/* media queries  */

@media screen and (max-width: 1024px) {
  .nav__toggle-btn {
    display: inline-block;
    background-color: transparent;
    font-size: 1.8rem;
    cursor: pointer;
  }
  .nav__toggle-btn svg {
    color: var(--color-gray-100);
  }
  .nav__links {
    position: absolute;
    top: 100%;
    right: 0;
    flex-direction: column;
    gap: 0;
    perspective: 400px;
  }
  .active-nav,
  .active-nav::after {
    display: none;
  }
  .nav__links li {
    height: 4rem;
    width: 100%;
    animation: navAnimation 600ms ease forwards;
    transform: rotateX(90deg);
    opacity: 0;
    transform-origin: top;
    box-shadow: -2rem 2rem 5rem rgba(#000, 0.5);
  }
  .nav__links li:nth-child(2) {
    animation-delay: 200ms;
  }
  .nav__links li:nth-child(3) {
    animation-delay: 400ms;
  }
  .nav__links li:nth-child(4) {
    animation-delay: 600ms;
  }
  .nav__links li:nth-child(5) {
    animation-delay: 800ms;
  }
  .nav__links li:nth-child(6) {
    animation-delay: 1s;
  }
  .nav__links li:nth-child(7) {
    animation-delay: 1.2s;
  }
  @keyframes navAnimation {
    to {
      transform: rotate(0);
      opacity: 1;
    }
  }
  .nav__links li a {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    padding: 1rem 5rem 1rem 3rem;
    background-color: var(--color-primary);
  }
  .show__nav {
    display: flex;
  }
  .hide__nav {
    display: none;
  }
}
